import Vue from 'vue'
import Router from 'vue-router'
import ExpenseList from '@/components/ExpenseList.vue'
import AddExpense from '@/components/AddExpense.vue'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'ExpenseList',
            component: ExpenseList
        },
        {
            path: '/add-expense',
            name: 'AddExpense',
            component: AddExpense
        }
    ]
})
